import React, { useEffect, useState } from "react";
import "./App.css";

import axios from "axios";

import Results from "./components/Results";

const App = () => {
  const gas_api =
    "https://script.google.com/macros/s/AKfycbyHzLTfo3_0Zml0tUE3dO8Zg8Q558PSZKvUdNlHW-RwTfaiN55QHxPDEG_ByKuI5-hd/exec";

  const [recipes, setRecipes] = useState([]);

  const [token, setToken] = useState(null);
  const [id, setId] = useState(null);

  const setting = (e) => {
    e.preventDefault();
    const inputId = document.getElementById("notionId").value;
    const inputToken = document.getElementById("notionToken").value;
    setId(inputId);
    setToken(inputToken);
  };

  const search = (e) => {
    e.preventDefault();
    const searchText = document.getElementById("searchText").value;
    console.log(`search: ${searchText}`);

    const params = new URLSearchParams();
    params.append("id", id);
    params.append("token", token);
    params.append("searchText", searchText);

    axios
      .post(gas_api, params)
      .then((res) => {
        console.log(res.data);
        setRecipes(res.data);
      })
      .catch((e) => {
        setRecipes([]);
        console.error(e);
      });
  };

  const reset = () => {
    setId(null);
    setToken(null);
  };

  useEffect(() => {
    const _id = localStorage.getItem("notion_id");
    const _token = localStorage.getItem("notion_token");
    setId(_id);
    setToken(_token);
  }, []);

  useEffect(() => {
    if (id) {
      localStorage.setItem("notion_id", id);
    }
  }, [id]);

  useEffect(() => {
    if (token) {
      localStorage.setItem("notion_token", token);
    }
  }, [token]);

  return (
    <main>
      {token && id ? (
        <>
          <button type="button" onClick={reset}>
            reset
          </button>
          <form onSubmit={search}>
            <input
              id="searchText"
              type="text"
              placeholder="キーワードを入力"
              autoFocus
            />
            <button type="submit">検索</button>
          </form>
          <Results recipes={recipes}></Results>
        </>
      ) : (
        <form onSubmit={setting}>
          <dl>
            <dt>ID</dt>
            <dd>
              <input id="notionId" type="text" />
            </dd>
          </dl>
          <dl>
            <dt>Token</dt>
            <dd>
              <input id="notionToken" type="text" />
            </dd>
          </dl>
          <button type="submit">設定</button>
        </form>
      )}
    </main>
  );
};

export default App;
